function buildDistanceInWordsLocale() {
  var distanceInWordsLocale = {
    lessThanXSeconds: {
      one: '   ',
      other: '  {{count}} '
    },
    xSeconds: {
      one: '1 ',
      other: '{{count}} '
    },
    halfAMinute: ' ',
    lessThanXMinutes: {
      one: '   ',
      other: '  {{count}} '
    },
    xMinutes: {
      one: '1 ',
      other: '{{count}} '
    },
    aboutXHours: {
      one: ' 1 ',
      other: ' {{count}} '
    },
    xHours: {
      one: '1 ',
      other: '{{count}} '
    },
    xDays: {
      one: '1 ',
      other: '{{count}} '
    },
    aboutXMonths: {
      one: ' 1 ',
      other: ' {{count}} '
    },
    xMonths: {
      one: '1 ',
      other: '{{count}} '
    },
    aboutXYears: {
      one: ' 1 ',
      other: ' {{count}} '
    },
    xYears: {
      one: '1 ',
      other: '{{count}} '
    },
    overXYears: {
      one: '  1 ',
      other: '  {{count}} '
    },
    almostXYears: {
      one: ' 1 ',
      other: ' {{count}} '
    }
  };

  function localize(token, count, options) {
    options = options || {};
    var result;

    if (typeof distanceInWordsLocale[token] === 'string') {
      result = distanceInWordsLocale[token];
    } else if (count === 1) {
      result = distanceInWordsLocale[token].one;
    } else {
      result = distanceInWordsLocale[token].other.replace('{{count}}', count);
    }

    if (options.addSuffix) {
      if (options.comparison > 0) {
        return ' ' + result;
      } else {
        return result + ' ';
      }
    }

    return result;
  }

  return {
    localize: localize
  };
}

module.exports = buildDistanceInWordsLocale;